@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;600;700&display=swap');

.App {
  display: flex;
  flex-direction: column;
  align-items: flex-start;     /* align to the left horizontally */
  justify-content: flex-start; /* align to the top vertically */
  height: 100vh;
  padding-top: 280px;          /* move typing text downward */
  padding-left: 10vw;          /* space from the left edge */
  background-color: #01153E; /* full navy background */
  color: white; /* default text color */
}

.typewriter {
  font-family: 'Raleway', sans-serif;
  font-size: 50px;
  color: white;
}

.cursor {
  display: inline-block;
  width: 2px;
  height: 50px; /* same height as your text */
  background-color: white;
  margin-left: 2px;
  animation: blink 1s step-end infinite;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow-x: hidden; /* prevent horizontal scroll */
}

.subtitle {
  font-size: 40px;
  margin-top: 20px;
  font-weight: 600;
  color: #b0bcd0;
}

.description {
  margin-top: 20px;
  max-width: 600px;
  font-size: 18px;
  line-height: 1.6;
  color: #a8b2d1;
}

.cta-button {
  margin-top: 30px;
  display: inline-block;
  padding: 12px 24px;
  border: 1px solid #64ffda;
  color: #64ffda;
  font-weight: 500;
  border-radius: 4px;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: rgba(100, 255, 218, 0.1);
}

html {
  scroll-behavior: smooth;
}

.section {
  min-height: 100vh;
  padding: 120px 10vw;
  color: white;
  background-color: #01153E;
  border-top: 1px solid #233554;
}

